import React from 'react';
import { memo } from 'react';
import { css } from '@linaria/core';

import { getCellStyle, getCellClassname, isCellEditable } from './utils';
import type { CellRendererProps } from './types';
import { useRovingCellRef } from './hooks';

const cellCopied = css`
  background-color: #ccccff;
`;

const cellCopiedClassname = `rdg-cell-copied ${cellCopied}`;

const cellDraggedOver = css`
  background-color: #ccccff;

  &.${cellCopied} {
    background-color: #9999ff;
  }
`;

const cellDraggedOverClassname = `rdg-cell-dragged-over ${cellDraggedOver}`;

const cellRangeSelected = css`
  background-color: rgba(0, 120, 215, 0.15);
`;

const cellRangeSelectedClassname = `rdg-cell-range-selected ${cellRangeSelected}`;

type MouseEventWithSelectionFlag = MouseEvent & {
  rdgSelectionHandledOnMouseDown?: boolean;
};

function Cell<R, SR>({
  column,
  colSpan,
  isCellSelected,
  isCellWithinSelectionRange,
  isCopied,
  isDraggedOver,
  row,
  rowIdx,
  dragHandle,
  onRowClick,
  onRowDoubleClick,
  onRowChange,
  selectCell,
  ...props
}: CellRendererProps<R, SR>) {
  const { ref, tabIndex, onFocus } = useRovingCellRef(isCellSelected);

  const { cellClass } = column;
  const className = getCellClassname(
    column,
    {
      [cellCopiedClassname]: isCopied,
      [cellDraggedOverClassname]: isDraggedOver,
      [cellRangeSelectedClassname]: isCellWithinSelectionRange
    },
    typeof cellClass === 'function' ? cellClass(row) : cellClass
  );

  function selectCellWrapper(openEditor?: boolean | null, extendSelection?: boolean) {
    selectCell(row, column, openEditor, extendSelection);
  }

  function handleClick(event: React.MouseEvent<HTMLDivElement>) {
    const editOnClick = column.editorOptions?.editOnClick;
    const selectionHandledOnMouseDown = (event.nativeEvent as MouseEventWithSelectionFlag)
      .rdgSelectionHandledOnMouseDown;
    if (editOnClick || !selectionHandledOnMouseDown) {
      selectCellWrapper(editOnClick, event.shiftKey);
    }
    onRowClick?.(row, column);
  }

  function handleContextMenu() {
    selectCellWrapper();
  }

  function handleDoubleClick() {
    selectCellWrapper(true);
    onRowDoubleClick?.(row, column);
  }

  return (
    <div
      role="gridcell"
      aria-colindex={column.idx + 1} // aria-colindex is 1-based
      aria-selected={isCellSelected}
      aria-colspan={colSpan}
      aria-readonly={!isCellEditable(column, row) || undefined}
      data-idx={column.idx}
      data-rowidx={rowIdx}
      ref={ref}
      tabIndex={tabIndex}
      className={className}
      style={getCellStyle(column, colSpan)}
      onClick={handleClick}
      onDoubleClick={handleDoubleClick}
      onContextMenu={handleContextMenu}
      onFocus={onFocus}
      {...props}
    >
      {!column.rowGroup && (
        <>
          <column.formatter
            column={column}
            row={row}
            rowIdx={rowIdx}
            isCellSelected={isCellSelected}
            onRowChange={onRowChange}
          />
          {dragHandle}
        </>
      )}
    </div>
  );
}

export default memo(Cell) as <R, SR>(props: CellRendererProps<R, SR>) => JSX.Element;
